import styles from "./skills.module.css";
export default function Skills() {
  return (
    <div className={`${styles.container}`} id="skills">
      <div className="">
        <h1 className={styles.title}>Skills</h1>
        <h3 className={styles.secondTitle}>My technical level</h3>
      </div>
      <div className={styles.wrapper}>
        <div className={`${styles.skillsWrap} col-md-10`}>
          <div className={`${styles.skillsGroup} col-md-6`}>
            {skills.map((skill: ISkill, index: number) => {
              if (index > 5) return null;
              return createSkillDiv(skill);
            })}
          </div>
          <div className={`${styles.skillsGroup} col-md-6`}>
            {skills.map((skill: ISkill, index: number) => {
              if (index <= 5) return null;
              return createSkillDiv(skill);
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
function createSkillDiv(skill: ISkill) {
  return (
    <div className={`${styles.skillData} col-md-5 `} key={skill.name}>
      <div className={styles.skillText}>
        <i className="align-middle">{skill.icon}</i>
        <span className="align-middle">{skill.name}</span>
      </div>
      <div className={styles.level}>
        <h5>{skill.level}</h5>
      </div>
    </div>
  );
}
